:root {
  --default-orange: #FF9100;
  --background-gray: #0F0F0F;
  --dark-gray: #0A0A0A;
  --light-gray: rgba(255,255,255,.6);
  --icon-button-bg-color: rgba(255,255,255, .25);
  --button1-left-color: #FD8B05;
  --button1-right-color: #EC5CB4;
  --button2-bg-color: rgba(253, 139, 5, .4);
  --button2-border-color: rgb(253, 139, 5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: 'Poppins', Arial, Helvetica, sans-serif;
  background-color: #0F0F0F;
  background-color: var(--background-gray);
  color: #fff;
}
h1 {
  font-size: 35px;
  font-weight: 700;
}
p {
  opacity: .8;
}

a {
  display: inline-block;
}

.highlight-text {
  color: #FF9100;
