}
.title-p {
  font-weight: 600;
  opacity: 1;
}
.loading-wheel {
  width: 45px;
  height: 45px;
  display: block;
  margin: auto;
  background-color: transparent;
  border-radius: 50%;
  border: 3px solid white;
  border-style: dashed;
}
.spinning-wheel {
  -webkit-animation: spin 3s linear infinite;
          animation: spin 3s linear infinite;
}


.hero-section {
  padding-top: 200px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.profile-picture {
  border: 5px solid #FF9100;
  border: 5px solid var(--default-orange);
  border-radius: 50%; 
}

.hero-content {
  max-width: 45%;
  padding-left: 90px;
}
.hero-title {
  padding-top: 39px;
}
.hero-description-title {
  margin: 90px 0 20px 0;
}
.hero-content > a {
  margin-top: 35px;
}


section#projects {
  text-align: center;
}
.projects-container {
  display: block;
  width: 100%;
  padding: 150px 15% 50px 15%;
  text-align: center;
}


.el_center-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-card {
  width: 740px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #0A0A0A;
  background-color: var(--dark-gray);
  margin-top: 47px;
  padding: 45px 55px 35px 55px;
  border-radius: 28px;
}
.email-wrapper {
  margin-top: 18px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.email-container {
  width: 65%;
  background-color: rgba(0,0,0, .40);
  padding: 8px 20px;
  border: 2px solid #707070;
  border-radius: 7px;
}

.social-card {
  margin-top: 50px;
  padding: 15px 25px 50px 25px;
  background-color: #1C1C1C;
  border-radius: 16px;
}
.social-card > nav {
  margin-top: 18px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}


