.project-container {
    position: relative;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    margin: 100px 0;
}

.project-featured_sticker {
    position: absolute;
    top: 0;
    left: 0;
    width: 85px;
    height: 85px;
    background-image: linear-gradient(45deg, var(--button1-left-color), var(--button1-right-color));
    display: flex;
    align-items: center;
    text-align: center;
    border-radius: 50%;
    font-size: 14px;
    font-weight: 500;
}

.project-title {
    margin-left: -200px;
    font-size: 64px;
    font-weight: 700;
    color: #DCDCDC;
    text-shadow: 0 0 14px rgba(0,0,0, .55);
}


.project-image-wrapper {
    position: relative;
    width: 100%;
    height: 397px;
    margin: 16px 0 0 14px;
}
.project-image {
    width: inherit;
    height: inherit;
    object-fit: cover;
    margin: inherit;
    border-radius: 12px;
    -webkit-filter: opacity(40%);
            filter: opacity(40%);
}

.project-role {
    font-size: 13px;
}

.project-info {
    text-align: left;
    width: 100%;
    margin-left: 60px;
    margin-bottom: 20px;
    z-index: 10;
}
.project-info > p {
    font-size: 12px;
    max-width: 70%;
}
.project-info > a {
    vertical-align: middle;
    margin-top: 30px;
    margin-right: 10px;
}


/*-- ODD ELEMENTS --*/
.project-container:nth-child(even) {
    flex-direction: row-reverse;
}
.project-container:nth-child(even) > .project-image-wrapper > .project-featured_sticker {
    left: unset;
    right: -30px;
}
.project-container:nth-child(even) > .project-info {
    text-align: right;
}
.project-container:nth-child(even) > .project-info > p {
    margin-left: auto;
}
.project-container:nth-child(even) > .project-info > .project-title {
    margin-right: -200px;
}

@media screen and (max-width: 1026px) {
    .project-container {
        flex-direction: column;
    }
    .project-container:nth-child(even) {
        flex-direction: column;
    }
    .project-image-wrapper {
        margin: 0;
        height: auto;
    }
    .project-featured_sticker {
        position: absolute;
        top: -8px;
        left: -7px;
        width: 65px;
        height: 65px;
        font-size: 10px;
        font-weight: 500;
    }
    .project-container:nth-child(even) > .project-image-wrapper > .project-featured_sticker {
        position: absolute;
