        height: 65px;
        font-size: 10px;
        font-weight: 500;
    }

    .project-title {
        margin-left: 0;
        font-size: 64px;
        font-weight: 700;
        color: #DCDCDC;
        text-shadow: 0 0 14px rgba(0,0,0, .55);
    }
    .project-info {
